<template>
  <div class="box">
    <Tabs v-model="tab_index" size="small" @on-click="handleTabClick">
      <TabPane
        v-for="item in tab_list"
        :key="item.id"
        :label="item.name"
        :name="item.name"
      >
        <EvaluationList v-if="current(item, '')" />
        <EvaluationStatisticsList v-if="current(item, '')" />
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
  import EvaluationList from '@/components/statistics/staff-data/consume/evaluation-list'
  import EvaluationStatisticsList from '@/components/statistics/staff-data/consume/evaluation-statistics-list'

  export default {
    name: 'Consume',
    components: {
      EvaluationList,
      EvaluationStatisticsList,
    },
    data: function () {
      return {
        tab_index: '',
        tab_list: [
          { name: '', id: 1 },
          { name: '', id: 2 },
        ],
      }
    },
    computed: {
      current() {
        return (item, name) => {
          return name === item.name && this.tab_index === name
        }
      },
    },
    methods: {
      handleTabClick(index) {
        this.tab_index = index
      },
    },
  }
</script>
<style lang="less" scoped></style>
